import React, { FC, ComponentPropsWithoutRef } from 'react';
import { default as MuiAlert, AlertProps as MuiAlertProps } from '@mui/material/Alert';
import CloseButton from '@elementor/ui/CloseButton';
import SvgIcon from '@elementor/ui/SvgIcon';
import { styled, createSlots, Theme, alpha } from '@elementor/ui/styles';
import { LINK_PSEUDO_SELECTORS } from '../styles/theme-config/constants';

interface AlertProps extends MuiAlertProps {
	/** The secondary action to display. It renders after the message, at the end of the alert before the action slot. */
	secondaryAction?: React.ReactNode;
}

const StyledAlert = styled( MuiAlert )( ( { theme, severity, color, variant } ) => {
	const variantsStyle = getVariantsStyle( severity, color, variant, theme );

	return {
		padding: theme.spacing( 1.5, 2 ),
		'& .MuiAlert-message': {
			width: '100%',
			padding: 0,
			minHeight: '30.75px',
			display: 'flex',
			flexDirection: 'row',
			flexWrap: 'wrap',
			gap: theme.spacing( 1.5 ),
		},
		'& .MuiAlertTitle-root': {
			marginBottom: 0,
			lineHeight: 'inherit',
			marginRight: theme.spacing( 0.5 ),
			...theme.typography.subtitle2,
			marginTop: 0,
		},
		'& .MuiAlert-icon': {
			padding: 0,
			paddingTop: theme.spacing( 0.5 ),
		},
		'& .MuiAlert-action': {
			padding: 0,
			marginLeft: theme.spacing( 1 ),
		},
		'&.MuiAlert-filledWarning': {
			color: theme.palette.common.white,
		},
		...variantsStyle,
	};
} );

export const { slots, classNames } = createSlots( 'Alert', [ 'actions', 'content' ] );

const StyledAlertContent = styled( 'div', slots.content )( () => ( {
	flexGrow: 1,
	paddingTop: '6px',
} ) );

const StyledAlertContentInnerContainer = styled( 'div', slots.content )( ( { theme } ) => ( {
	alignItems: 'center',
	display: 'flex',
	flexWrap: 'wrap',
	gap: theme.spacing( 0.25 ),
	maxWidth: '800px',
} ) );

const AlertContent: FC<ComponentPropsWithoutRef<'div'>> = ( { children, ...props } ) => {
	return (
		<StyledAlertContent { ...props }>
			<StyledAlertContentInnerContainer>
				{ children }
			</StyledAlertContentInnerContainer>
		</StyledAlertContent>
	);
};

const StyledAlertActions = styled( 'div' )( ( { theme } ) => ( {
	display: 'flex',
	alignItems: 'flex-start',
	flexWrap: 'wrap',
	gap: theme.spacing( 1 ),
} ) );

// Modified reason: Allowing multiple actions with responsive layout.
const Alert = React.forwardRef<HTMLDivElement, AlertProps>( ( { onClose, action, secondaryAction, children, ...props }, ref ) => {
	const hasActions = Boolean( action || secondaryAction );

	return (
		<StyledAlert
			iconMapping={ {
				success: <SuccessIcon />,
				error: <ErrorIcon />,
				info: <InfoIcon />,
				warning: <WarningIcon />,
			} }
			{ ...props }
			ref={ ref }
			action={
				onClose
					? (
						<CloseButton
							color="inherit"
							onClick={ onClose }
							slotProps={ {
								icon: {
									fontSize: 'small',
								},
							} }
							title={ props.closeText }
							aria-label={ props.closeText }
						/>
					)
					: false
			}
		>
			<AlertContent className={ classNames.content }>
				{ children }
			</AlertContent>

			{ hasActions && (
				<StyledAlertActions className={ classNames.actions }>
					{ secondaryAction }

					{ action }
				</StyledAlertActions>
			) }
		</StyledAlert>
	);
} ) as React.FC<AlertProps>;

Alert.defaultProps = {
	closeText: 'Close',
	severity: 'success',
};

export default Alert;

function getVariantsStyle(
	severity: AlertProps['severity'],
	color: AlertProps['color'],
	variant: AlertProps['variant'],
	theme: Theme,
) {
	// In Mui, the color prop has a higher priority than the severity prop.
	const semanticColor = color || severity;

	if ( ! semanticColor ) {
		return {};
	}

	if ( variant === 'filled' ) {
		return {
			'& .MuiButton-containedInherit': {
				color: theme.palette[ semanticColor ].main,
				backgroundColor: 'rgba(255, 255, 255, 1)',
				'&:hover': {
					backgroundColor: 'rgba(255, 255, 255, .96)',
				},
			},
			'& .MuiButton-outlinedInherit:hover': {
				backgroundColor: theme.palette[ semanticColor ].dark,
			},

			// Setting the pseudo-classes color to prevent the override of global CSS with higher specificity than the Button component.
			'& a.MuiButtonBase-root.MuiButton-containedInherit': {
				[ LINK_PSEUDO_SELECTORS ]: {
					color: theme.palette[ semanticColor ].main,
				},
			},
		};
	}

	return {
		'&.MuiAlert-root': {
			color: theme.palette.text.secondary,
		},
		'& .MuiCloseButton-root': {
			color: theme.palette.action.active,
		},
		'& .MuiButton-containedInherit': {
			color: theme.palette[ semanticColor ].contrastText,
			backgroundColor: theme.palette[ semanticColor ].main,
			'&:hover': {
				backgroundColor: theme.palette[ semanticColor ].dark,
			},
		},
		'& .MuiButton-outlinedInherit': {
			borderColor: theme.palette[ semanticColor ].main,
			color: theme.palette[ semanticColor ].main,
			'&:hover': {
				backgroundColor: alpha( theme.palette[ semanticColor ].main, 0.08 ),
			},
		},

		// Setting the pseudo-classes color to prevent the override of global CSS with higher specificity than the Button component.
		'& a.MuiButtonBase-root.MuiButton-containedInherit': {
			[ LINK_PSEUDO_SELECTORS ]: {
				color: theme.palette[ semanticColor ].contrastText,
			},
		},
		'& a.MuiButtonBase-root.MuiButton-outlinedInherit': {
			[ LINK_PSEUDO_SELECTORS ]: {
				color: theme.palette[ semanticColor ].main,
			},
		},
	};
}

