import React from 'react';
import clsx from 'clsx';
import IconButton, { IconButtonProps } from '@elementor/ui/IconButton';
import SvgIcon, { SvgIconProps } from '@elementor/ui/SvgIcon';
import { styled, useThemeProps, createSlots } from '@elementor/ui/styles';
import './CloseButton.types';

export interface CloseButtonProps extends Omit<IconButtonProps, 'size' | 'color'> {
	color?: 'default' | 'inherit';
	slotProps?: {
		icon?: SvgIconProps;
	};
}

const XIcon = React.forwardRef<SVGSVGElement, SvgIconProps>( ( props, ref ) => {
	return (
		<SvgIcon viewBox="0 0 24 24" { ...props } ref={ ref }>
			<path fillRule="evenodd" clipRule="evenodd" d="M18.5303 5.46967C18.8232 5.76256 18.8232 6.23744 18.5303 6.53033L6.53033 18.5303C6.23744 18.8232 5.76256 18.8232 5.46967 18.5303C5.17678 18.2374 5.17678 17.7626 5.46967 17.4697L17.4697 5.46967C17.7626 5.17678 18.2374 5.17678 18.5303 5.46967Z" />
			<path fillRule="evenodd" clipRule="evenodd" d="M5.46967 5.46967C5.76256 5.17678 6.23744 5.17678 6.53033 5.46967L18.5303 17.4697C18.8232 17.7626 18.8232 18.2374 18.5303 18.5303C18.2374 18.8232 17.7626 18.8232 17.4697 18.5303L5.46967 6.53033C5.17678 6.23744 5.17678 5.76256 5.46967 5.46967Z" />
		</SvgIcon>
	);
} );

const { slots, classNames } = createSlots( 'CloseButton', [ 'root', 'icon' ] );

export type CloseButtonSlots = keyof typeof slots;

const Root = styled( IconButton, slots.root )<{ ownerState: CloseButtonProps }>( {} );

const Icon = styled( XIcon, slots.icon )<{ ownerState: CloseButtonProps }>( {} );

const CloseButton = React.forwardRef<HTMLButtonElement, CloseButtonProps>( ( inProps, ref ) => {
	const props = useThemeProps( { props: inProps, name: slots.root.name } );

	const { slotProps = {}, ...rootProps } = props;

	return (
		<Root
			{ ...rootProps }
			size="small"
			ref={ ref }
			className={ clsx( [ [ classNames.root, rootProps.className ] ] ) }
			ownerState={ props }
		>
			<Icon
				{ ...slotProps.icon }
				className={ clsx( [ classNames.icon, slotProps.icon?.className ] ) }
				ownerState={ props }
			/>
		</Root>
	);
} ) as React.FC<CloseButtonProps>;

CloseButton.defaultProps = {
	'aria-label': 'close',
	color: 'default',
};

export default CloseButton;
